.game {
    margin: auto;
    font-family: Roboto;
}

.controls {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.board {
    margin: auto;
    width:100%;
    margin-bottom: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.memory-card.flipped {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
}

.flip {
    -webkit-perspective: 800;
    perspective:800;
    position: relative;
}

.memory-card {
    margin:10px;
    width: 100%;
    height: 100%;
    -webkit-transform-style: preserve-3d;
    -webkit-transition: 0.8s;
    transform-style: preserve-3d;
    transition: 0.8s;
}

.face {
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    z-index: 2;
    font-family: Georgia;
    font-size: 3em;
    text-align: center;
}

.front {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
    background: blue;
    background: white;
    color: black;
    cursor: pointer;
}

.back {
    position: absolute;
    z-index: 1;
    background: black;
    color: white;
    cursor: pointer;
}

img {
    border-radius: 10px;
}

img:hover {
    cursor: pointer;
    transform: scale(1.01);
}

@media only screen and (min-width: 600px) {
    .game {
        width: 900px;
    }

    .board {
        width: 700px;
        height: 900px;
    }

    .flip {
        width: 150px;
        height: 200px;
        margin: 10px;
    }
}

@media only screen and (max-width: 600px) {
    
    .board {
        width: 370px;
        height: 500px;
    }
    
    .flip {
        width: 75px;
        height: 100px;
        margin: 2px;
    }
}